import { SelectionModel } from '@angular/cdk/collections';
import { FlatTreeControl } from '@angular/cdk/tree';
import { Component, Injectable, Inject,OnInit } from '@angular/core';
import { MatDialog, MAT_DIALOG_DATA, MatDialogRef } from '@angular/material';
import { MatTreeFlatDataSource, MatTreeFlattener } from '@angular/material/tree';
import { BehaviorSubject } from 'rxjs';
import { ClubTreeService } from '../../../../../../core/auth/_services/club-tree.service'
import { Router } from '@angular/router'
import {FormGroup,FormBuilder,Validators} from '@angular/forms'
import { LayoutUtilsService, MessageType, QueryParamsModel } from '../../../../../../core/_base/crud';
import { MustMatch } from '../must-match.validator';
@Injectable()
@Component({
  selector: 'club_tree_data',
  templateUrl: 'club_tree_data.html',

})
export class ClubTreeDataComponent {
  treeItemForm: FormGroup;
  treeItem;
	hasFormErrors: boolean = false;
  errormessage: string ;
  viewLoading: boolean = false;
  loadingAfterSubmit: boolean = false;
  resource=false
  constructor(
    public dialogRef: MatDialogRef<ClubTreeDataComponent>,
    private itemFB:FormBuilder,
    private router: Router,
    private clubTreeService: ClubTreeService,
    @Inject(MAT_DIALOG_DATA) public data: any,
    private layoutUtilsService: LayoutUtilsService) {

      this.data = data['node']
      //console.log(this.data)
      if(this.data['type']=='resources')
      this.resource=true
      else
      this.resource=false
      //console.log(this.resource)
      if (this.data['_id'])
        this.prepareDataToShow()
      else
         this.createForm()
  }
  ngOnInit() {

    //console.log(this.data)
    if (this.data['_id']||this.data['id'])
      this.prepareDataToShow()
    else
       this.createForm()
  }
  createForm() {
    console.log('bgbng' ,this.data)
		this.treeItemForm = this.itemFB.group({
      name:[this.data['name'], Validators.required],
      City:[this.data['city'], Validators.required],
			Country:[this.data['country'], Validators.required],
			email:[this.data['email'], Validators.email],
      brief:[this.data['brief'],Validators.required],
      title:[this.data['title'],Validators.required],
      lat:[this.data['lat'],Validators.required],
      lang:[this.data['lang'],Validators.required],
      username:[this.data['username'],Validators.required],
      password:[this.data['password'],Validators.min(8)] })
     // confirmPassword: ['', Validators.required})
    // }, {
    //     validator: MustMatch('password', 'confirmPassword')
    // })
    //console.log(this.treeItemForm)
	}
  //method to access data from server
  prepareDataToShow() {
    this.resource=false
    const edit = this.data['edit']
    const type = this.data['type']
    if (this.data['type'] == 'branch') {
      this.clubTreeService.getBranchData(this.data['pub_key']).subscribe(res => {
        if (res && res['result']) {

          this.data = res['user']
          this.data['edit'] = edit
          this.data['type'] = type
          this.data['header'] = `${this.data['name']}  informaton`
          this.createForm()
        }
      })
    }
    else if (this.data['type'] == 'units') {
      this.clubTreeService.getUnitData(this.data['pub_key']).subscribe(res => {
        if (res && res['result']) {

          this.data = res['user']
          this.data['edit'] = edit
          this.data['type'] = type
          this.data['header'] = `${this.data['name']}  informaton`
          //console.log(res['user'])
          this.createForm()
        }
      })
    }
    else if (this.data['type'] == 'resources') {

      this.clubTreeService.getResourceData(this.data['_id']).subscribe(res => {
        //console.log(res,)
        if (res && res['result']) {

          this.data = res['data'][0]
          this.data['edit'] = edit
          this.data['type'] = type
          this.data['header'] = `${this.data['title']}  informaton`
          //console.log(this.data)
          this.resource=true
          this.createForm()
        }
      })
    }
  }
  addItem() {
    //console.log()
    this.resource=false
    let user,club_key;
    if(localStorage.getItem('user'))
    {
       user=JSON.parse(localStorage.getItem('user'))['username']
      club_key=JSON.parse(localStorage.getItem('user'))['pub_key']
    }
    this.checkedForm()
    this.resource=false
    const controls=this.treeItemForm.controls;
    if (this.data['type'] == 'units') {
    this.clubTreeService.addUnits({'branch_key':this.data['branch_key'],'units_key':this.data['units_key'],
    'username':controls['username'].value,'name':controls['name'].value,
     'email':controls['email'].value,'city':controls['City'].value,
    'country':controls['Country'].value,'brief':controls['brief'].value,'lat':controls['lat'].value,'lang':controls['lang'].value,'password':controls['password'].value}).subscribe(res=>{
      //console.log(res)
      if (res['result']) {
        this.afterupdate()
      }
      else {
        this.errormessage=res['error']

      }
    })

    }
    else if (this.data['type'] == 'resources') {
      this.resource=true
      this.clubTreeService.addResources({'branch_key':this.data['branch_key'],'units_key':this.data['units_key'],'club_key':club_key,
      'title':controls['title'].value,
        }).subscribe(res=>{
          //console.log(res)
        if (res['result']) {
          this.afterupdate()
        }
        else {
         this.errormessage=res['error']
        }
      })
    }
    else if (this.data['type'] == 'branch') {
      this.clubTreeService.addBranch({
    'username':controls['username'].value,'name':controls['name'].value,
     'email':controls['email'].value,'country':controls['Country'].value,
     'city':controls['City'].value,'brief':controls['brief'].value,'lat':controls['lat'].value,'lang':controls['lang'].value,'password':controls['password'].value}).subscribe(res=>{
      if (res['result']) {
        this.afterupdate()
      }
      else {
        this.hasFormErrors=true
        this.errormessage=res['error']

      }
    })
    }
  }
  onSubmit() {
    this.resource=false
    const controls = this.treeItemForm.controls;
   this.checkedForm()
    if (this.data['type'] == 'branch') {
      this.clubTreeService.updateBranchData({
        'pub_key': this.data['pub_key'],
        'name': controls['name'].value,
        'username': controls['username'].value,
        'country':controls['Country'].value,
        'city':controls['City'].value,
        'email': controls['email'].value,
        'brief':controls['brief'].value,
        'lat':controls['lat'].value,
        'lang':controls['lang'].value,
        'password':controls['password'].value
      }).subscribe(res => {
        //console.log(res, this.data)
        if (res['result']) {

          this.afterupdate()
        }
        else {
          this.errormessage=res['error']
        }
      })

    }
    else if (this.data['type'] == 'units') {
      this.clubTreeService.updateUnitData({
        'pub_key': this.data['pub_key'],
        'username': controls['username'].value,
        'name': controls['name'].value,
        'country':controls['Country'].value,
        'city':controls['City'].value,
        'email': controls['email'].value,
        'brief':controls['brief'].value,
        'lat':controls['lat'].value,
        'lang':controls['lang'].value,
        'password':controls['password'].value

      }).subscribe(res => {
        //console.log(res)
        if (res['result']) {
          this.afterupdate()
        }
        else {

          this.errormessage = res['error'];
        }
      })
    }
    else if (this.data['type'] == 'resources') {
      this.resource=true
      //console.log(this.data)
    this.clubTreeService.updateResourceData({'title':controls['title'],'_id':this.data['_id']}).subscribe(res => {
      //console.log(res)
      if (res['result']) {
        this.afterupdate()
      }
      else {

        this.errormessage = res['error'];
      }
    })
    }

  }
  afterupdate() {
    this.dialogRef.close({});
    //console.log(this.router.url)
    if (this.router.url == '/default/user-management/clubtree')
      this.router.navigate(['/default/user-management/clubtrees'])
    else
      this.router.navigate(['/default/user-management/clubtree'])
   
    setTimeout(() => {
      this.layoutUtilsService.showActionNotification(`successfuly update ${this.data['type']}`, MessageType.Update);
    }, 1000)
  }



	/**
	 * Close alert
	 *
	 * @param $event: Event
	 */
  onAlertClose($event) {
    this.hasFormErrors = false;
  }

checkedForm()
{
  this.hasFormErrors = false;
  const controls = this.treeItemForm.controls;
  //console.log(this.treeItemForm)
  if (this.treeItemForm.invalid) {
    Object.keys(controls).forEach(controlName =>
    {
      controls[controlName].markAsTouched()
    }
    );
    //console.log('invalid')
    this.hasFormErrors = true;
    return;
  }
}
save()
{
  //console.log(this.data)
  if(this.data['_id']||this.data['id'])
  {
    this.onSubmit()
  }
  else{
    this.addItem()
  }
}

get valid() { return this.treeItemForm.controls; }

}
