import { AfterViewInit, AfterViewChecked } from '@angular/core';
// Angular
import { Component ,OnInit, ElementRef, ViewChild, ChangeDetectionStrategy, OnDestroy, ChangeDetectorRef } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
// Material
import { SelectionModel } from '@angular/cdk/collections';
import { MatPaginator, MatSort, MatSnackBar, MatDialog, MatTableDataSource } from '@angular/material';
// RXJS
import { debounceTime, distinctUntilChanged, tap, skip, take, delay, map } from 'rxjs/operators';
import { fromEvent, merge, Observable, of, Subscription } from 'rxjs';
// LODASH
import { each, find } from 'lodash';

// NGRX
import { Store, select } from '@ngrx/store';
import { AppState } from '../../../../core/reducers';

// Services
import { LayoutUtilsService, MessageType, QueryParamsModel } from '../../../../core/_base/crud';
import { UserService } from '../../../../core/auth/_services'
import { InvitationsEditDialogComponent } from './invitations-edit/invitations-edit.dialog.component';

import { SubheaderService } from '../../../../core/_base/layout';


// Table with EDIT item in MODAL
// ARTICLE for table with sort/filter/paginator
// https://blog.angular-university.io/angular-material-data-table/
// https://v5.material.angular.io/components/table/overview
// https://v5.material.angular.io/components/sort/overview
// https://v5.material.angular.io/components/table/overview#sorting
// https://www.youtube.com/watch?v=NSt9CI3BXv4
@Component({
	selector: 'kt-invitations',
	templateUrl: './invitations.component.html',
	styleUrls: ['../usser.management.scss'],
	changeDetection: ChangeDetectionStrategy.OnPush
})
export class InvitationComponent implements OnInit, OnDestroy {
	// Table fields
	dataSource;
	listData:MatTableDataSource<any>;
	selection = new SelectionModel<any>(true, []);
	displayedColumns = [ 'id', 'admin' ,'Place' , 'username','status', 'actions'];
	@ViewChild(MatPaginator) paginator: MatPaginator;
//@ViewChild(MatSort) sort: MatSort;
	// Filter fields
	@ViewChild('searchInput') searchInput: ElementRef;
	lastQuery: QueryParamsModel;
	SearchKey: string;
	selectedUser;
	hasMessages: boolean = false;
	checkdata: boolean = false;
	filterStatus: string = '';
	filterInvitedBy: string = '';
	getallinviteby : any;
	order : number = -1;
	// Subscriptions
	private subscriptions: Subscription[] = [];

	/**
	 *
	 * @param activatedRoute: ActivatedRoute
	 * @param store: Store<AppState>
	 * @param router: Router
	 * @param layoutUtilsService: LayoutUtilsService
	 * @param subheaderService: SubheaderService
	 */
	constructor(
		private activatedRoute: ActivatedRoute,
		private store: Store<AppState>,
		private router: Router,
		private layoutUtilsService: LayoutUtilsService,
		public dialog: MatDialog,
		private ser: UserService,
	    private changeDetectorRefs: ChangeDetectorRef

	) { }
	/**
	 * @ Lifecycle sequences => https://angular.io/guide/lifecycle-hooks
	 */

	/**
	 * On init
	 */
	ngOnInit() {
		this.getallinviteby=[];
		this.getInvitaions();
		this.getUsresWhichSendEmails();
	}

	ngAfterViewInit()
	{
		this.getInvitaions()

	}
	/**
	 * On Destroy
	 */
	ngOnDestroy() {
		this.subscriptions.forEach(el => el.unsubscribe());
	}
	addInvitations() {
		if(this.checkedpermission('sendinvitations'))
		{
		const dialogRef = this.dialog.open(InvitationsEditDialogComponent, { data: {} });
		}
		else
		{
		alert('Sorry, you do not have permission to send invitation')
		}
	}

	/**
	 * Close alert
	 *
	 * @param $event: Event
	 */
	onAlertClose($event) {
		this.hasMessages = false;
	}


	getInvitaions(pages = 1, prepage = 30, search = '',status='',author_key='',orderby='',order=-1) {

		this.ser.getAllInvitations(pages, prepage, search,status,author_key,orderby,order).subscribe(

			res => {
               console.log(res['result'])
				if (res['result']) {
					this.dataSource = new MatTableDataSource( res['result']);
					this.changeDetectorRefs.detectChanges();
					//this.listData.sort=this.sort
					//this.listData.paginator=this.paginator

				}
			}
		)
	}

	getUsresWhichSendEmails(){
		this.ser.getUsresWhichSendEmails().subscribe(
			res=>{
				this.getallinviteby = res['result'];
			}
		)
	}

	revokeinvitation(id) {
		this.ser.reVokeInvitations(id).subscribe(

			res=>{
				if(this.router.url == '/default/user-management/invitations')
					this.router.navigateByUrl("/default/user-management/invitation");
				else this.router.navigateByUrl("/default/user-management/invitations");
				setTimeout(()=>{
					this.layoutUtilsService.showActionNotification(res['message'], MessageType.Update);
				}, 1000)
			}
		)
	}

	resendinvitation(id) {
		this.ser.reSendInvitations(id).subscribe(
			res => {

				if(this.router.url == '/default/user-management/invitations')
					this.router.navigateByUrl("/default/user-management/invitation");
				else this.router.navigateByUrl("/default/user-management/invitations");
				setTimeout(()=>{
					this.layoutUtilsService.showActionNotification(res['message'], MessageType.Update);
				}, 1000)
			}
		)
	}
	applyFilter(orderby='') {

		if(this.SearchKey && this.SearchKey.length > 0)
			var search = this.SearchKey.trim().toLowerCase();
		else var search='';
		if (this.filterStatus && this.filterStatus.length > 0)
			var status = this.filterStatus;
		else status = '';

		if (this.filterInvitedBy && this.filterInvitedBy.length > 0)
			var author_key = this.filterInvitedBy;
		else author_key = '';

		if(orderby !=''){
			if(this.order == -1) this.order=1;
			else if(this.order == 1) this.order=-1;
		}
		this.getInvitaions(null, null, search,status,author_key,orderby,this.order)
	}
	onSearchClear() {
		this.searchInput.nativeElement.value = ''
		this.applyFilter()
	}
	nextPage(event) {

		this.getInvitaions(event.pageIndex + 1, event.pageSize, null)
	}
	//checked if there is inivitations or not
	checkedinvitations() {
		if (this.dataSource) {
			if (this.dataSource.data) {
				return true
			}
			else return false
		}
		else false
	}
	checkedpermission(key: String) {
		let checked = false
		if (localStorage.getItem('user')) {

			if (JSON.parse(localStorage.getItem('user'))) {
				let user = JSON.parse(localStorage.getItem('user'))

				if (user['allPrivilidge']) {
					const privilidge = user['allPrivilidge']

					Object.keys(privilidge).forEach(role => {
						Object.keys(privilidge[role]).forEach(pri => {

							let per = (privilidge[role][pri].toString()).trim()
							if (key.trim() == per) {
								checked = true
							}
						})
					})
				}
			}
		}

		return checked
	}


	fetchUsers(){
		
	}
}
