// Angular
import { Component, OnInit, OnDestroy, Input } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
// RxJS
import { BehaviorSubject, Observable, of, Subscription } from 'rxjs';
// NGRX
import { Store, select } from '@ngrx/store';
import { Update } from '@ngrx/entity';
import { AppState } from '../../../../../core/reducers';
// Layout
import { SubheaderService, LayoutConfigService } from '../../../../../core/_base/layout';
import { LayoutUtilsService, MessageType } from '../../../../../core/_base/crud';
import { UserService } from '../../../../../core/auth/_services'
// Services and Models
import {
	User,
	UserUpdated,
	Address,
	SocialNetworks,
} from '../../../../../core/auth';
import { FF_VOLUME_UP } from '@angular/cdk/keycodes';



@Component({
	selector: 'kt-user-edit',
	templateUrl: './user-edit.component.html',
})
export class UserEditComponent implements OnInit, OnDestroy {
	// Public properties

	userId$: Observable<number>;
	oldUser;
	user;
	user_id;
	selectedTab: number = 0;
	loading$: Observable<boolean>;
	addressSubject = new BehaviorSubject({});
	soicialNetworksSubject = new BehaviorSubject({});
	userForm: FormGroup;
	hasFormErrors: boolean = false;
	// Private properties
	private subscriptions: Subscription[] = [];

	/**
	 * Component constructor
	 *
	 * @param activatedRoute: ActivatedRoute
	 * @param router: Router
	 * @param userFB: FormBuilder
	 * @param subheaderService: SubheaderService
	 * @param layoutUtilsService: LayoutUtilsService
	 * @param store: Store<AppState>
	 * @param layoutConfigService: LayoutConfigService
	 */
	constructor(private activatedRoute: ActivatedRoute,
		private router: Router,
		private userFB: FormBuilder,
		private subheaderService: SubheaderService,
		private layoutUtilsService: LayoutUtilsService,
		private store: Store<AppState>,
		private layoutConfigService: LayoutConfigService,
		private userService: UserService,

	) {


	}

	/**
	 * @ Lifecycle sequences => https://angular.io/guide/lifecycle-hooks
	 */

	/**
	 * On init
	 */
	ngOnInit() {
		this.user_id = this.activatedRoute.snapshot.paramMap.get('id')
		//this.user_id=this.router.url.split('/')[4]
		console.log(this.user_id)
		if (this.activatedRoute.snapshot.queryParamMap.get("shift")) this.selectedTab = 4;
		// this.loading$ = this.store.pipe(select(selectUsersActionLoading));
		const routeSubscription = this.activatedRoute.params.subscribe(params => {
			const id = params['id'];
			if (id && this.user_id) {
				this.userService.getUserbyId(this.user_id).subscribe(res => {
					if (res) {
						this.user = res['users'][0];
						this.addressSubject.next(this.preparAddressToShow());
						this.soicialNetworksSubject.next(this.preparSocialNetworkToShow());
						this.oldUser = Object.assign({}, this.user);
						this.initUser();
						this.preparAddressToShow()
						this.preparSocialNetworkToShow()
						//this.soicialNetworksSubject.next(this.preparSocialNetworkToShow())
						this.userService.changeuser(this.user)

					}
				}
				)
			}
		})
		this.subscriptions.push(routeSubscription);
	}

	ngOnDestroy() {
		this.subscriptions.forEach(sb => sb.unsubscribe());
	}

	/**
	 * Init user
	 */
	initUser() {
		this.createForm();
		console.log(this.user)
		if (!this.user) {   //id
			this.subheaderService.setTitle('Create user');
			this.subheaderService.setBreadcrumbs([
				{ title: 'User Management', page: `user-management` },
				{ title: 'Users', page: `user-management/users` },
				{ title: 'Create user', page: `user-management/users/add` }
			]);
			return;
		}
		this.subheaderService.setTitle('Edit user');
		this.subheaderService.setBreadcrumbs([
			{ title: 'User Management', page: `user-management` },
			{ title: 'Users', page: `user-management/users` },
			{ title: 'Edit user', page: `user-management/users/edit`, }
		]);
	}

	/**
	 * Create form
	 */
	createForm() {
		this.userForm = this.userFB.group({
			username: [this.user.username, Validators.required],
			name: [this.user.name, Validators.required],
			email: [this.user.email, Validators.email],
			phone: [this.user.phone],
			//	companyName: [this.user.companyName],
			//	occupation: [this.user.occupation]
		});
	}

	/**
	 * Redirect to list
	 *
	 */
	goBackWithId() {
		const url = `${this.layoutConfigService.getCurrentMainRoute()}/user-management/users`;
		this.router.navigateByUrl(url, { relativeTo: this.activatedRoute });
	}

	/**
	 * Refresh user
	 *
	 * @param isNew: boolean
	 * @param id: number
	 */
	refreshUser(isNew: boolean = false, id = 0) {
		let url = this.router.url;
		if (!isNew) {
			this.router.navigate([url], { relativeTo: this.activatedRoute });
			return;
		}

		url = `${this.layoutConfigService.getCurrentMainRoute()}/user-management/users/edit`;
		this.router.navigateByUrl(url, { relativeTo: this.activatedRoute });
	}

	/**
	 * Reset
	 */
	reset() {
		this.user = Object.assign({}, this.oldUser);
		this.createForm();
		this.hasFormErrors = false;
		this.userForm.markAsPristine();
		this.userForm.markAsUntouched();
		this.userForm.updateValueAndValidity();
	}

	/**
	 * Save data
	 *
	 * @param withBack: boolean
	 */
	onSumbit(withBack: boolean = false, role = false) {

		console.log('this.selectedTab', this.selectedTab)
		if (this.selectedTab < 3) {
			if (this.checkedpermission('updateuserdata')) {
				this.updateBasicInfo()
			} else {
				alert('Sorry,You do not have permission to update user')
			}

		}

		else if (this.selectedTab === 3) {

			if (role) {
				const editedUser = this.prepareUser();
				this.userService.currentUser.subscribe(res => {
					if (res) {
						if (this.user['privilidge']) {
							if (this.checkedpermission('editprivilidges')) {

								if (!res['role'].typeOfRole) {
									res['role']['typeOfRole'] = this.user['privilidge']['type_id']
								}
								this.userService.updateUserPrvilidge(this.user['privilidge']['id'], { privilidge: res['role'].privilidge, type: res['role'].typeOfRole }).subscribe(res => {
									if (res['result']) {
										console.log('userrr', this.user)
										this.updateUser(editedUser, true, `User successfully has been saved.`)

										//this.showToast('secondary ','User successfully has been saved')
									}
									else {
										this.updateUser(editedUser, false, `Sorry user hasnt been saved ,try again.`)
										//this.showToast('secondary ','Sorry user hasnt been saved ,try again')
									}
									this.selectedTab = 0;
								//	this.userService.changeuser(null)
								})
							}
							else {
								alert('Sorry,You dont have permission to update privilidge')
							}

						}
						else {
							if (this.checkedpermission('addprivilidges')) {
								if (!res['role']) {
									alert('You must choose privilige before saving')
								}
								else {
									if (!res['role'].privilidge || !res['role'].typeOfRole || !this.user['pub_key']) {
                                       alert('Sorry,incorrect data try again')
									}
									else {
										this.userService.AddUserPrivilidge(res['role'].privilidge, res['role'].typeOfRole, this.user['pub_key']).subscribe(res => {
											if (res['result']) {
												console.log('userrr', this.user)
												this.updateUser(editedUser, true, `User successfully has been saved.`)

												//this.showToast('secondary ','User successfully has been saved')
											}
											else {
												this.updateUser(editedUser, false, `Sorry user hasnt been saved ,try again.`)
												//this.showToast('secondary ','Sorry user hasnt been saved ,try again')

											}
											this.selectedTab = 0;
										//	this.userService.changeuser(null)
										})
									}
								}
							}

							else {
								alert('Sorry,You dont have permission to add privilidge')
							}
						}
					}
				}).unsubscribe()

			}
		}
	}

	/**
	 * Returns prepared data for save
	 */
	prepareUser(): any {
		const controls = this.userForm.controls;

		console.log('edit user', this.user)
		if (this.user) {
			this.user.address = this.addressSubject.value;
			this.user.socialNetworks = this.soicialNetworksSubject.value;
			this.user.username = controls['username'].value;
			this.user.email = controls['email'].value;
			this.user.name = controls['name'].value;

			return {
				email: this.user.email, username: this.user.username, name: this.user.name, pub_key: this.user['pub_key'],
				'addressLine': this.user.addressLine
				, 'city': this.user.city
				, 'state': this.user.state
				, 'postCode': this.user.postCode
				, 'linkedIn': this.user.socialNetworks.linkedIn
				, 'facebook': this.user.socialNetworks.facebook
				, 'twitter': this.user.socialNetworks.twitter
				, 'instagram': this.user.socialNetworks.instagram
			}

		}
	};
	/**
	 * Add User
	 *
	 * @param _user: User
	 * @param withBack: boolean
	 */
	addUser(_user: User, withBack: boolean = false) {
		// this.store.dispatch(new UserOnServerCreated({ user: _user }));
		// const addSubscription = this.store.pipe(select(selectLastCreatedUserId)).subscribe(newId => {
		const message = `New user successfully has been added.`;
		this.layoutUtilsService.showActionNotification(message, MessageType.Create, 5000, true, true);

		if (withBack) {
			this.goBackWithId();
		} else {
			this.refreshUser(true);
		}

		// });
		// this.subscriptions.push(addSubscription);

	}

	/**
	 * Update user
	 *
	 * @param _user: User
	 * @param withBack: boolean
	 */
	updateUser(_user: User, withBack: boolean = false, message) {
		// Update User
		// tslint:disable-next-line:prefer-const

		// const updatedUser: Update<User> = {
		// 	id: _user._userId,
		// 	changes: _user
		// };
		// this.store.dispatch(new UserUpdated( { partialUser: updatedUser, user: _user }));

		this.layoutUtilsService.showActionNotification(message, MessageType.Update, 5000, true, true);
		if (withBack) {
			this.goBackWithId();
		} else {
			this.refreshUser(true);
		}
	}

	/**
	 * Returns component title
	 */
	getComponentTitle() {
		let result = 'Create user';
		if (!this.user || !this.user._userId) { //id
			return result;
		}

		result = `Edit user - ${this.user.name}`;
		return result;
	}

	/**
	 * Close Alert
	 *
	 * @param $event: Event
	 */
	onAlertClose($event) {
		this.hasFormErrors = false;
	}

	preparSocialNetworkToShow() {
		if (this.user) {
			const SocialNetworks = {
				linkedIn: this.user.linkedIn
				, facebook: this.user.facebook,
				twitter: this.user.twitter,
				instagram: this.user.instagram
			}
			this.user.socialNetworks = SocialNetworks;
			return SocialNetworks
		}
		else return { linkedIn: '', twitter: '', instagram: '',facebook:''}
	}
	preparAddressToShow() {
		if (this.user) {
			const Address = {
				'addressLine': this.user.addressLine,
				'city': this.user.city,
				'state': this.user.state,
				'postCode': this.user.postCode
			}
			this.user.address = Address
			return Address
		}
		else return { 'addressLine': '', 'city': '', 'state': '', 'postCode': '' }
	}
	showToast(type, text) {

		// this.toaster.open({
		//   text: text,
		//   caption:'Edit user role',
		//   type: type,
		// });
	}
	checkedpermission(key: String) {
		let checked = false
		if (localStorage.getItem('user')) {

			if (JSON.parse(localStorage.getItem('user'))) {
				let user = JSON.parse(localStorage.getItem('user'))

				if (user['allPrivilidge']) {
					const privilidge = user['allPrivilidge']

					Object.keys(privilidge).forEach(role => {
						Object.keys(privilidge[role]).forEach(pri => {

							let per = (privilidge[role][pri].toString()).trim()
							if (key.trim() == per) {
								checked = true
							}
						})
					})
				}
			}
		}

		return checked
	}


	updateBasicInfo() {
		this.hasFormErrors = false;
		const controls = this.userForm.controls;
		/** check form */

		if (this.userForm.invalid) {
			Object.keys(controls).forEach(controlName => {
				controls[controlName].markAsTouched()
			}
			);

			this.hasFormErrors = true;
			this.selectedTab = 0;
			return;
		}
		const editedUser = this.prepareUser();
		console.log(editedUser)
		this.userService.updateUserData(editedUser).subscribe(res => {
			if (res['result']) {
				console.log('userrr', this.user)
				this.updateUser(editedUser, true, `User successfully has been saved.`)
				//this.showToast('secondary ','User successfully has been saved')

			}
			else {
				this.updateUser(editedUser, false, `Sorry user hasnt been saved ,try again.`)
				//this.showToast('secondary ','Sorry user hasnt been saved ,try again')
			}
			this.selectedTab = 0;
			//this.userService.changeuser(null)
		});
	}
}
