import { BaseModel } from '../../../../../../../core/_base/crud';

export class Shift extends BaseModel {
        club_key: string;
        branch_key: string;
        units_key: string;
        resource_id: string;
        day_per_week: string;
        user_key: string;
        shift_type: string;
        from_time: string;
        to_time: string;
        from_day: string;
        to_day: string;
        status: string;

    clear(): void {
      this.club_key = '';
      this.branch_key = '';
      this.units_key = '';
      this.resource_id = '';
      this.day_per_week = '';
      this.user_key = '';
      this.shift_type = '';
      this.from_time = '';
      this.to_time = '';
      this.from_day = '';
      this.to_day = '';
      this.status = '';
    }
}
