import { AfterViewInit, AfterViewChecked } from '@angular/core';
// Angular
import { Component, OnInit, ElementRef, ViewChild, ChangeDetectionStrategy, OnDestroy, ChangeDetectorRef } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
// Material
import { SelectionModel } from '@angular/cdk/collections';
import { MatPaginator, MatSort, MatSnackBar, MatTableDataSource } from '@angular/material';
// RXJS
import { debounceTime, distinctUntilChanged, tap, skip, take, delay, map } from 'rxjs/operators';
import { fromEvent, merge, Observable, of, Subscription } from 'rxjs';
// LODASH
import { each, find } from 'lodash';

// NGRX
import { Store, select } from '@ngrx/store';
import { AppState } from '../../../../../core/reducers';

// Services
import { LayoutUtilsService, MessageType, QueryParamsModel } from '../../../../../core/_base/crud';
import {UserService} from '../../../../../core/auth/_services'
// Models
import {
	User,
	Role,
	
	UserDeleted,
	UsersPageRequested,
	selectUserById,
	selectAllRoles
} from '../../../../../core/auth';
import { SubheaderService } from '../../../../../core/_base/layout';


// Table with EDIT item in MODAL
// ARTICLE for table with sort/filter/paginator
// https://blog.angular-university.io/angular-material-data-table/
// https://v5.material.angular.io/components/table/overview
// https://v5.material.angular.io/components/sort/overview
// https://v5.material.angular.io/components/table/overview#sorting
// https://www.youtube.com/watch?v=NSt9CI3BXv4
@Component({
	selector: 'kt-users-list',
	templateUrl: './users-list.component.html',

	styleUrls: ['../../usser.management.scss'],
	changeDetection: ChangeDetectionStrategy.OnPush
})
export class UsersListComponent implements OnInit, OnDestroy {
	// Table fields
	dataSource;
	listData:MatTableDataSource<any>;
	displayedColumns = ['select', 'id', 'username', 'email', 'fullname', '_roles', 'actions'];
	@ViewChild(MatPaginator) paginator: MatPaginator;
	//@ViewChild(MatSort) sort: MatSort;
	// Filter fields
	@ViewChild('searchInput') searchInput: ElementRef;
	lastQuery: QueryParamsModel;
	// Selection
	selection = new SelectionModel<User>(true, []);
	usersResult: User[] = [];
	allRoles: Role[] = [];
	SearchKey:string='';
	filterStatus: string = '';
	filterRole: string = '';
	filterPrivilidge: string = '';
	getallroles : any;
	order : number = -1;
	allPrivilidge : any;
	selectedUser;
	// Subscriptions
	private subscriptions: Subscription[] = [];

	/**
	 *
	 * @param activatedRoute: ActivatedRoute
	 * @param store: Store<AppState>
	 * @param router: Router
	 * @param layoutUtilsService: LayoutUtilsService
	 * @param subheaderService: SubheaderService
	 */
	constructor(
		private activatedRoute: ActivatedRoute,
		private store: Store<AppState>,
		private router: Router,
		private layoutUtilsService: LayoutUtilsService,
		private subheaderService: SubheaderService,
		private cdr: ChangeDetectorRef,
		private ser :UserService
		) {}
	/**
	 * @ Lifecycle sequences => https://angular.io/guide/lifecycle-hooks
	 */

	/**
	 * On init
	 */
	ngOnInit() {
		
	              	this.getuser();
			
		
		this.getallroles=[];
		if(this.checkedpermissions('getprivilidgestype')){
		this.getallrole();
		}


		// load roles list
		const rolesSubscription = this.store.pipe(select(selectAllRoles)).subscribe(res => this.allRoles = res)
		this.subscriptions.push(rolesSubscription);

		// Set title to page breadCrumbs
		this.subheaderService.setTitle('User management');


	}


	checkedpermissions(key: String) {
		let checked = false
		if (localStorage.getItem('user')) {

			if (JSON.parse(localStorage.getItem('user'))) {
				let user = JSON.parse(localStorage.getItem('user'))

				if (user['allPrivilidge']) {
					const privilidge = user['allPrivilidge']

					Object.keys(privilidge).forEach(role => {
						Object.keys(privilidge[role]).forEach(pri => {

							let per = (privilidge[role][pri].toString()).trim()
							if (key.trim() == per) {
								checked = true
							}
						})
					})
				}
			}
		}

		return checked
	}


	/**
	 * On Destroy
	 */
	ngOnDestroy() {
		this.subscriptions.forEach(el => el.unsubscribe());
	}


	/** FILTRATION */
	filterConfiguration(): any {
		const filter: any = {};
		const searchText: string = this.searchInput.nativeElement.value;

		filter.lastName = searchText;

		filter.username = searchText;
		filter.email = searchText;
		filter.fillname = searchText;
		return filter;
	}

	/** ACTIONS */
	/**
	 * Delete user
	 *
	 * @param _item: User
	 */
	deleteUser(_item: User) {
		
		const _title: string = 'User Delete';
		const _description: string = 'Are you sure to permanently delete this user?';
		const _waitDesciption: string = 'User is deleting...';
		const _deleteMessage = `User has been deleted`;
        const _failedToDelete=`sorry there is problem to delete this user try again`
		const dialogRef = this.layoutUtilsService.deleteElement(_title, _description, _waitDesciption);
		dialogRef.afterClosed().subscribe(res => {
			if (!res) {
				return;
			}

			this.ser.deleteUser(_item['pub_key']).subscribe(res=>{
				if(res['result'])
				{

					this.listData.data.slice(this.listData.data.indexOf(_item),1)
					this.getuser()
					this.store.dispatch(new UserDeleted({ id: _item['_id'] }));
					this.layoutUtilsService.showActionNotification(_deleteMessage, MessageType.Delete);
				}
				else{
                    this.layoutUtilsService.showActionNotification(_failedToDelete, MessageType.Delete);
				}
			})

		});
	}


	activateuser(id,status) {
		const _title: string = 'User Delete';
		const _description: string = 'Are you sure to permanently delete this user?';
		const _waitDesciption: string = 'User is deleting...';
		const dialogRef = this.layoutUtilsService.deleteElement(_title, _description, _waitDesciption);
		dialogRef.afterClosed().subscribe(res => {
			if (!res) {
				return;
			}
		if(status == 'Active') var message = 'User Activated Successfully';
		else  var message = 'User DeActivated Successfully';
		var ids = new Array();ids.push(id);
		this.ser.activateuser(ids,status).subscribe(
			result=>{
				if(this.router.url == '/default/user-management/users')
					this.router.navigateByUrl("/default/user-management/user");
				else this.router.navigateByUrl("/default/user-management/users");
				setTimeout(()=>{
					this.layoutUtilsService.showActionNotification(message, MessageType.Update);
				}, 1000)
			}
		)
	});
	}

	/**
	 * Fetch selected rows
	 */
	fetchUsers() {
		const messages = [];
		this.selection.selected.forEach(elem => {
			messages.push({
				text: `${elem.name}, ${elem.email}`,
				type: `${elem['type'].toString()}`,
				status: elem.username
			});
		//this.ser.getUserPriviliges().forEach(item=>{console.log(item,elem['havePrivilidge'])})
		});
		this.layoutUtilsService.fetchElements(messages);
	}

	activeallusers(status) {
		var ids = [];
		this.selection.selected.forEach(elem => {
			ids.push(elem['pub_key']);
		});
		console.log(ids);
		if(status == 'Active') var message = 'User Activated Successfully';
		else  var message = 'User DeActivated Successfully';

		this.ser.activateuser(ids,status).subscribe(
			res=>{
				if(this.router.url == '/default/user-management/users')
					this.router.navigateByUrl("/default/user-management/user");
				else this.router.navigateByUrl("/default/user-management/users");
				setTimeout(()=>{
					this.layoutUtilsService.showActionNotification(message, MessageType.Update);
				}, 1000)
			}
		)
	}

	/**
	 * Check all rows are selected
	 */
	isAllSelected(): boolean {
		const numSelected = this.selection.selected.length;
		const numRows = this.usersResult.length;
		return numSelected === numRows;
	}

	/**
	 * Toggle selection
	 */
	masterToggle() {
		if (this.selection.selected.length === this.usersResult.length) {
			this.selection.clear();
		} else {
			this.usersResult.forEach(row => this.selection.select(row));
		}
	}

	/* UI */
	/**
	 * Returns user roles string
	 *
	 * @param user: User
	 */
	getUserRolesStr(user: User): string {
		const titles: string[] = [];
		if(user['havePrivilidge'])
        {
		//this.ser.getUserPriviliges().forEach(item=>{console.log(item),user['havePrivilidge']})
		return
	    }
	}

	editUser(_item) {
	    this.ser.changeuser(_item);
		this.router.navigate(['/default/user-management/users',_item._id,'edit',_item._id]);
	}
	onSearchClear()
    {
		this.SearchKey=""
		this.applyFilter()
	}
	/*
	*
	*
	*/
	//method to  search online
	applyFilter(orderby=''){
		if(this.SearchKey && this.SearchKey.length > 0)
			var search = this.SearchKey.trim().toLowerCase();
		else var search='';
		if (this.filterStatus && this.filterStatus.length > 0)
			var status = this.filterStatus;
		else status = '';

		if (this.filterRole && this.filterRole.length > 0)
			var role = this.filterRole;
		else role = '';

		if (this.filterPrivilidge && this.filterPrivilidge.length > 0)
			var privilidge = this.filterPrivilidge;
		else privilidge = '';

		if(orderby !=''){
			if(this.order == -1) this.order=1;
			else if(this.order == 1) this.order=-1;
		}

		this.getuser(null,null,search,status,role,privilidge,orderby,this.order);
	}

	/*
	*
	*
	*/
  nextPage(event){
		//console.log(event)
        this.getuser(event.pageIndex+1,event.pageSize,null)
	}
	/*
	*
	*
	*/
	getuser(pages=1,prepage=30,search=null,status='',role='',privilidge='',orderby='',order=-1)
	{	if(this.checkedpermissions('getallusers')){
		this.ser.getallusers(pages,prepage,search,status,role,privilidge,orderby,order).subscribe(
			res=>{
				//console.log(res)
				this.listData=new MatTableDataSource(res['users'])
				//this.listData.sort=this.sort
				this.listData.paginator=this.paginator
				this.cdr.detectChanges()
			}
		)
	}
}

	/*
	*
	*
	*/
	getallrole()
	{
		this.ser.getallrole().subscribe(
			res=>{
				//console.log(res)
				this.getallroles = res['result'];
			}
		)

		var privilidge = JSON.parse(localStorage.getItem('user'))['allPrivilidge'];
		var result = [];
		Object.keys(privilidge).forEach(function(key) {
			var items = new Array();
			Object.keys(privilidge[key]).forEach(function(item){
				items.push({key:item,value:privilidge[key][item]})
			});
			result.push({key,value:items})
		});
		console.log(result);
		this.allPrivilidge = result;
	}
	//check if listdata has data to show
	checkedListData()
	{
		if(this.listData)
		{
			if(this.listData.data)
			{
				return true;
			}
			else false
		}
		else false
	}
	checkedpermission(key)
	{

		if( localStorage.getItem('user'))
		{

			if(JSON.parse(localStorage.getItem('user'))['allPrivilidge'])
			{
			if(JSON.parse(localStorage.getItem('user'))['allPrivilidge']['Users Mangement'])
			{
				if(key=='del')
				{

			  if(JSON.parse(localStorage.getItem('user'))['allPrivilidge']['Users Mangement']['Delete user'])
			  return true
				}
				else if(key=='update')
				{

					if( JSON.parse(localStorage.getItem('user'))['allPrivilidge']['Users Mangement']['Update User'])
					return true
				}
			}
		}
		}

		return false
	}
}
