/**
* Node for to-do item
*/
import { browserRefresh } from '../../../../app.component';
export class TodoItemNode {
  children: TodoItemNode[];
  item: string;
}

/** Flat to-do item node with expandable and level information */
export class TodoItemFlatNode {
  item: string;
  level: number;
  expandable: boolean;
}

/**
* The Json object for to-do list data.
*/


/**
* Checklist database, it can build a tree structured Json object.
* Each node in Json object represents a to-do item or a category.
* If a node is a category, it has children items and new items can be added under the category.
*/
@Injectable()
export class ChecklistDatabase {

  dataChange = new BehaviorSubject<TodoItemNode[]>([]);
    
  get data(): TodoItemNode[] { 
    if(this.dataChange)
     return this.dataChange.value; }

  constructor(private clubTreeSevice: ClubTreeService,
    private changeDetectorRefs: ChangeDetectorRef,
    private layoutConfigService: LayoutConfigService
  ) {
    this.initialize();
  }

  initialize() {
  
    // Build the tree nodes from Json object. The result is a list of `TodoItemNode` with nested
    //     file node as children.
    this.clubTreeSevice.getUnitsData().subscribe(res => {
      if (res && res['result']) {
        res['data'].forEach(elem => {
          elem['type'] = 'branch'
          elem['branch_key']=elem['pub_key']
        })
        const data = this.buildFileTree(res['data'], 0);
        console.log(data)
        this.dataChange.next(data);
        this.changeDetectorRefs.detectChanges();
      }
    },
    error=>{console.log(error)})

  }

  /**
  * Build the file structure tree. The `value` is the Json object, or a sub-tree of a Json object.
  * The return value is the list of `TodoItemNode`.
  */
  // buildFileTree(obj: object, level: number): TodoItemNode[] {
  //   console.log(obj)
  //   return Object.keys(obj).reduce<TodoItemNode[]>((accumulator, key) => {
  //     const value = obj[key];
  //     const node = new TodoItemNode();
  //     console.log(value)
  //     node.item=value['name']
  //     Object.values(value).forEach(value=>{
  //      if(typeof value =='object')
  //      {
  //        console.log(typeof value)
  //        node.children= this.buildFileTree(value,level+1)
  //      }
  //     })


  //     return accumulator.concat(node);
  //   }, []);
  //   }

  buildFileTree(obj, level: number): TodoItemNode[] {

    return Object.keys(obj).reduce<TodoItemNode[]>((accumulator, key, currentindex) => {
      const node = new TodoItemNode();
      
      node['pub_key'] = obj[key]['pub_key']
      node['parent'] = obj[key]['parent']
      node['_id'] = obj[key]['_id']
      node['type'] = obj[key]['type']
      
      if(obj[key]['type']=='branch')
      {
        node['branch_key']=obj[key]['pub_key']
      }
      if(obj['type']=='units')
      {
        node['units_key']=obj['pub_key']
      }
      if(obj['branch_key'])
      {
        node['branch_key']=obj['branch_key']
      }
      if(obj['units_key'])
      {
        node['units_key']=obj[key]['units_key']
      }
     
      if (key == 'units') {
       
        if(obj['branch_key'])
        {
          node['branch_key']=obj['branch_key']
        }
        if(obj['units_key'])
        {
          node['units_key']=obj['units_key']
        }
        node['pub_key'] = obj['pub_key']
        node['type'] = 'units'
      }
      if (key == 'resources') {
        node['pub_key'] = obj['pub_key']
        node['type'] = 'resources'
      }
      if (key == '0' || parseInt(key)) {
        node.item = obj[key]['name']
      }
      else {
        node.item = key;
      }

      const notImportantitem = ['_id', 'pub_key', 'parent', 'name', 'type','branch_key']
      const value = obj[key];

      if (value != null) {
        if (typeof value === 'object') {
          if (!(key == '0' || parseInt(key))) {
            if (value && value.length > 0) {
              value['pub_key'] = obj['pub_key']
              
              value.forEach(elem => {
                elem['type'] = key
                elem['branch_key']=obj['branch_key']

              })
            }
          }


          node.children = this.buildFileTree(value, level + 1);
        } else {
          node.item = value;
        }


        if (!notImportantitem.includes(key)) {
          return accumulator.concat(node);
        }
        else
          return accumulator.concat();
      }

    }
      , []);
  }

  /** Add an item to to-do list */
  insertItem(parent: TodoItemNode, node): TodoItemNode {
    //  console.log(parent)
    if (!parent.children) {
      parent.children = [];
    }
    let newItem = node as TodoItemNode;
    console.log(newItem)
    parent.children.push(newItem);
    this.dataChange.next(this.data);
    return newItem;
  }

  getParentFromNodes(node: TodoItemNode): TodoItemNode {
    for (let i = 0; i < this.data.length; ++i) {
      const currentRoot = this.data[i];
      const parent = this.getParent(currentRoot, node);
      if (parent != null) {
        return parent;
      }
    }
    return null;
  }

  getParent(currentRoot: TodoItemNode, node: TodoItemNode): TodoItemNode {
    if (currentRoot.children && currentRoot.children.length > 0) {
      for (let i = 0; i < currentRoot.children.length; ++i) {
        const child = currentRoot.children[i];
        if (child === node) {
          return currentRoot;
        } else if (child.children && child.children.length > 0) {
          const parent = this.getParent(child, node);
          if (parent != null) {
            return parent;
          }
        }
      }
    }
    return null;
  }


  insertItemAbove(node: TodoItemNode, nodechild): TodoItemNode {
    const parentNode = this.getParentFromNodes(node);
    //  console.log(parentNode)

    const newItem = nodechild as TodoItemNode;
    if (parentNode != null) {
      parentNode.children.splice(parentNode.children.indexOf(node), 0, newItem);
    } else {
      this.data.splice(this.data.indexOf(node), 0, newItem);
    }
    this.dataChange.next(this.data);
    console.log(this.data)
    return newItem;
  }

  insertItemBelow(node: TodoItemNode, nodechild): TodoItemNode {
    const parentNode = this.getParentFromNodes(node);
    const newItem = nodechild as TodoItemNode;
    if (parentNode != null) {
      parentNode.children.splice(parentNode.children.indexOf(node) + 1, 0, newItem);
    } else {
      this.data.splice(this.data.indexOf(node) + 1, 0, newItem);
    }
    this.dataChange.next(this.data);
    return newItem;
  }
  updateItem(node: TodoItemNode, nodechild) {
    node = nodechild
    this.dataChange.next(this.data);
  }


  deleteItem(node: TodoItemNode) {
    this.deleteNode(this.data, node);
    this.dataChange.next(this.data);
  }

  copyPasteItem(from: TodoItemNode, to: TodoItemNode): TodoItemNode {
      console.log(from,to)
    const newItem = this.insertItem(to, { 'item': from['item'], 'pub_key': from['pub_key'], 'type': from['type'], '_id': from['_id'], 'parent': from['parent'] });
    if (from.children) {
      from.children.forEach(child => {
        this.copyPasteItem(child, newItem);
      });
    }
    return newItem;
  }

  copyPasteItemAbove(from: TodoItemNode, to: TodoItemNode): TodoItemNode {
    const newItem = this.insertItemAbove(to, { 'item': from['item'], 'pub_key': from['pub_key'], 'type': from['type'], '_id': from['_id'], 'parent': from['parent'] });
    if (from.children) {
      from.children.forEach(child => {
        this.copyPasteItem(child, newItem);
      });
    }
    return newItem;
  }

  copyPasteItemBelow(from: TodoItemNode, to: TodoItemNode): TodoItemNode {
    const newItem = this.insertItemBelow(to, { 'item': from['item'], 'pub_key': from['pub_key'], 'type': from['type'], '_id': from['_id'], 'parent': from['parent'] });
    if (from.children) {
      from.children.forEach(child => {
        this.copyPasteItem(child, newItem);
      });
    }
    return newItem;
  }
  deleteNode(nodes: TodoItemNode[], nodeToDelete: TodoItemNode) {
    const index = nodes.indexOf(nodeToDelete, 0);
    if (index > -1) {
      nodes.splice(index, 1);
    } else {
      nodes.forEach(node => {
        if (node.children && node.children.length > 0) {
          this.deleteNode(node.children, nodeToDelete);
        }
      });
    }
  }
}

/**
* @title Tree with checkboxes
*/
@Component({
  selector: 'club_tree.component',
  styleUrls: ['./club_tree.component.css'],
  templateUrl: 'club_tree.component.html',
  providers: [ChecklistDatabase],
  entryComponents:[],
  encapsulation: ViewEncapsulation.None
})


export class ClubTreeComponent {
  /** Map from flat node to nested node. This helps us finding the nested node to be modified */
  flatNodeMap = new Map<TodoItemFlatNode, TodoItemNode>();

  /** Map from nested node to flattened node. This helps us to keep the same object for selection */
  nestedNodeMap = new Map<TodoItemNode, TodoItemFlatNode>();

  /** A selected parent node to be inserted */
  selectedParent: TodoItemFlatNode | null = null;

  /** The new item's name */
  newItemName = '';

  treeControl: FlatTreeControl<TodoItemFlatNode>;

  treeFlattener: MatTreeFlattener<TodoItemNode, TodoItemFlatNode>;

  dataSource: MatTreeFlatDataSource<TodoItemNode, TodoItemFlatNode>;

  /**Drag and drop */
  dragNode: any;
  dragNodeExpandOverWaitTimeMs = 300;
  dragNodeExpandOverNode: any;
  dragNodeExpandOverTime: number;
  dragNodeExpandOverArea: string;
  @ViewChild('emptyItem') emptyItem: ElementRef;

  /** The selection for checklist */
  checklistSelection = new SelectionModel<TodoItemFlatNode>(true /* multiple */);

  constructor(private database: ChecklistDatabase, private treeClubService: ClubTreeService, public dialog: MatDialog,
    private layoutUtilsService: LayoutUtilsService,
    private treeclubservice: ClubTreeService,
    public snackBar: MatSnackBar,
    private changeDetectorRefs: ChangeDetectorRef) {
    this.treeFlattener = new MatTreeFlattener(this.transformer, this.getLevel,
      this.isExpandable, this.getChildren);
    this.treeControl = new FlatTreeControl<TodoItemFlatNode>(this.getLevel, this.isExpandable);
    this.dataSource = new MatTreeFlatDataSource(this.treeControl, this.treeFlattener);
    // this.changeDetectorRefs.detectChanges()
  
    database.dataChange.subscribe(data => {
      this.dataSource.data = data;
    });
  }

  //config data of dialog of show data and edit


  getLevel = (node: TodoItemFlatNode) => node.level;

  isExpandable = (node: TodoItemFlatNode) => node.expandable;

  getChildren = (node: TodoItemNode): TodoItemNode[] => node.children;

  hasChild = (_: number, _nodeData: TodoItemFlatNode) => _nodeData.expandable;

  hasNoContent = (_: number, _nodeData: TodoItemFlatNode) => _nodeData.item === '';

  /**
  * Transformer to convert nested node to flat node. Record the nodes in maps for later use.
  */
  transformer = (node: TodoItemNode, level: number) => {

    const existingNode = this.nestedNodeMap.get(node);
    const flatNode = existingNode && existingNode.item === node.item
      ? existingNode
      : new TodoItemFlatNode();
    flatNode.item = node.item;
    flatNode.level = level;
    flatNode['pub_key'] = node['pub_key']
    if(node['branch_key'])
    flatNode['branch_key']=node['branch_key']
    if(node['units_key'])
    flatNode['units_key']=node['units_key']
    flatNode['_id'] = node['_id']
    if (node['_id'] == undefined && node['pub_key'] == undefined && node['parent'] == undefined) {
      //console.log( this.database.getParentFromNodes(node)['pub_key'])
      console.log(node)
      flatNode['pub_key'] = this.database.getParentFromNodes(node)['pub_key']
      //this.database.updateItem(node,flatNode)
      //console.log(flatNode)
    }
    if (node.children)
      flatNode['child'] = node['children'].length
    flatNode['type'] = node['type']
    flatNode.expandable = !!node.children;
    this.flatNodeMap.set(flatNode, node);
    this.nestedNodeMap.set(node, flatNode);
    return flatNode;
  }

  /** Whether all the descendants of the node are selected */
  descendantsAllSelected(node: TodoItemFlatNode): boolean {
    const descendants = this.treeControl.getDescendants(node);
    return descendants.every(child => this.checklistSelection.isSelected(child));
  }

  /** Whether part of the descendants are selected */
  descendantsPartiallySelected(node: TodoItemFlatNode): boolean {
    const descendants = this.treeControl.getDescendants(node);
    const result = descendants.some(child => this.checklistSelection.isSelected(child));
    return result && !this.descendantsAllSelected(node);
  }

  /** Toggle the to-do item selection. Select/deselect all the descendants node */
  todoItemSelectionToggle(node: TodoItemFlatNode): void {
    this.checklistSelection.toggle(node);
    const descendants = this.treeControl.getDescendants(node);
    this.checklistSelection.isSelected(node) ? this.checklistSelection.select(...descendants)
      : this.checklistSelection.deselect(...descendants);
  }

  /** Select the category so we can insert the new item. */
  addNewItem(node: TodoItemFlatNode) {
    const parentNode = this.flatNodeMap.get(node);
    this.database.insertItem(parentNode!, {});
    this.treeControl.expand(node);
  }

  /** Save the node to database */
  saveNode(node: TodoItemFlatNode, itemValue: string) {
    const nestedNode = this.flatNodeMap.get(node);
    this.database.updateItem(nestedNode!, itemValue);
  }
  Show_EditData(node, editable) {
    if(!editable)
    {
    if(this.checkedpermission('getallusers'))
    {
      node['edit'] = editable
      const data = { node }
      this.dialog.open(ClubTreeDataComponent, { data });
    }
    else{
      this.open()
      //alert('You dont have permission to get this data')
    }
    }
    else if(editable){
      if(node._id)
      {
       
        if(this.checkedpermission('updateuserdata'))
        {
          node['edit'] = editable
          const data = { node }
          this.dialog.open(ClubTreeDataComponent, { data });
        }
        else{
          this.open()
         // alert('You dont have permission to get this data')
        }
      }
      else if(!node._id)
      {
        if(this.checkedpermission('createuser'))
        {
          node['edit'] = editable
          const data = { node }
          this.dialog.open(ClubTreeDataComponent, { data });
        }
        else{
          this.open()
      //    alert('You dont have permission to get this data')
        }
      }
    }


  }
  open() {
   let message = 'Oh Snap ! You dont have permission to get this data';
    let actionButtonLabel = '';
   let action = true;
   let setAutoHide= true;
   let autoHide = 4000;
   let horizontalPosition: MatSnackBarHorizontalPosition = 'center';
   let verticalPosition: MatSnackBarVerticalPosition = 'top';
    
   let addExtraClass = false;
    let config = new MatSnackBarConfig();
    config.verticalPosition = verticalPosition;
    config.horizontalPosition = horizontalPosition;
    config.duration = setAutoHide ? autoHide : 0;
  //  config['extraClasses'] = addExtraClass ? ['warn-snackbar'] : undefined;
    config.panelClass=['warn-snackbar'];
    this.snackBar.open(message, action ? actionButtonLabel : undefined, config);
  }
  //checked the permission of user to do any action 
  checkedpermission(key: String) {
		let checked = false
		if (localStorage.getItem('user')) {

			if (JSON.parse(localStorage.getItem('user'))) {
				let user = JSON.parse(localStorage.getItem('user'))

				if (user['allPrivilidge']) {
					const privilidge = user['allPrivilidge']

					Object.keys(privilidge).forEach(role => {
						Object.keys(privilidge[role]).forEach(pri => {

							let per = (privilidge[role][pri].toString()).trim()
							if (key.trim() == per) {
								checked = true
							}
						})
					})
				}
			}
		}

		return checked
	}
  deleteItem(node) {

    if(this.checkedpermission('deleteuserdata')){
    const _title: string = node['type'];
    const _description: string = `Are you sure to permanently delete this ${node['type']}?`;
    const _waitDesciption: string = `${node['type']} is deleting...`;
    const _deleteMessage = `${node['type']} has been deleted`;
    if (node['type'] == 'branch') {

      const dialogRef = this.layoutUtilsService.deleteElement(_title, _description, _waitDesciption);
      dialogRef.afterClosed().subscribe(res => {
        if (!res) {
          return;

        }
        this.treeClubService.DeleteBranch(node['pub_key']).subscribe(res => {
          console.log(res)
          if (res['result']) {
            this.database.deleteItem(this.flatNodeMap.get(node))
            this.layoutUtilsService.showActionNotification(_deleteMessage, MessageType.Delete);
          }
        });
      })

    }
    if (node['type'] == 'units') {

      const dialogRef = this.layoutUtilsService.deleteElement(_title, _description, _waitDesciption);
      dialogRef.afterClosed().subscribe(res => {
        if (!res) {
          return;

        }
        this.treeClubService.DeleteUnit(node['pub_key']).subscribe(res => {
          if (res['result']) {
            this.database.deleteItem(this.flatNodeMap.get(node))
            this.layoutUtilsService.showActionNotification(_deleteMessage, MessageType.Delete);

            console.log(res)
          }
        });
      })

    }
    if (node['type'] == 'resources') {

      const dialogRef = this.layoutUtilsService.deleteElement(_title, _description, _waitDesciption);
      dialogRef.afterClosed().subscribe(res => {
        if (!res) {
          return;
        }
        this.treeClubService.DeleteResource(node['_id']).subscribe(res => {
          if (res['result']) {
            this.database.deleteItem(this.flatNodeMap.get(node))
            this.layoutUtilsService.showActionNotification(_deleteMessage, MessageType.Delete);
          }

        })
      });
    }
}
else {
  this.open()
  //alert('You dont have permission to delete ')
}
}






  handleDragStart(event, node) {
    
    // Required by Firefox (https://stackoverflow.com/questions/19055264/why-doesnt-html5-drag-and-drop-work-in-firefox)
    event.dataTransfer.setData('foo', 'bar');
    event.dataTransfer.setDragImage(this.emptyItem.nativeElement, 0, 0);
    this.dragNode = node;
    console.log('dragNode',this.dragNode)
    this.treeControl.collapse(node);
  }

  handleDragOver(event, node) {
    event.preventDefault();
   

    // Handle node expand
    if (node === this.dragNodeExpandOverNode) {
      if (this.dragNode !== node && !this.treeControl.isExpanded(node)) {
        if ((new Date().getTime() - this.dragNodeExpandOverTime) > this.dragNodeExpandOverWaitTimeMs) {
          this.treeControl.expand(node);
        }
      }
    } else {
      this.dragNodeExpandOverNode = node;
      this.dragNodeExpandOverTime = new Date().getTime();
    }

    // Handle drag area
    const percentageX = event.offsetX / event.target.clientWidth;
    const percentageY = event.offsetY / event.target.clientHeight;
    if (percentageY < 0.25) {
      this.dragNodeExpandOverArea = 'above';
    } else if (percentageY > 0.75) {
      this.dragNodeExpandOverArea = 'below';
    } else {
      this.dragNodeExpandOverArea = 'center';
    }
  }

  handleDrop(event, node) {
    event.preventDefault();
    let DragNode = this.flatNodeMap.get(this.dragNode);
    let DropArea = this.flatNodeMap.get(node);
    const _title: string = node['type'];
    const _description: string = `Are you sure to permanently drag ${DragNode['type']} to ${DropArea['type']}?`;
    const _waitDesciption: string = `${DragNode['type']} is draging...`;
    const _unadraggableMessage = `may be there is problem to drag this element ,try again and choose correctly area to drop `;
    
    console.log(this.dragNode)
    console.log(this.dragNode, node, this.flatNodeMap.get(this.dragNode), this.flatNodeMap.get(node))
  
    if (node !== this.dragNode && this.dragNode['type'] != 'branch'&&this.dragNode['type']==node['type']) {
      const dialogRef = this.layoutUtilsService.dragElement(_title, _description, _waitDesciption);
      dialogRef.afterClosed().subscribe(res => {
        console.log(res)
        if (!res) {
          
          return;}
      console.log(this.dragNode)
      if (!DragNode['_id'] ) {
        if (DragNode.children && DragNode.children.length > 0) {
          DragNode.children.forEach(dragElem => {
            this.handelDragElem(this.flatNodeMap.get(node),dragElem)
            dragElem['parent'] = DropArea['pub_key']
            if (DragNode.item == 'resources') {
              this.updateResource(dragElem['parent'], dragElem['_id'], node['type'])
            }
            if (DragNode.item == 'units') {
              console.log('units')
              this.updateUnit(dragElem['pub_key'], DropArea['pub_key'], node['type'])
            }
          })
          this.database.deleteItem(this.flatNodeMap.get(this.dragNode));
        }
      }
      else if (node['_id'] ) {
       
      if(DropArea['children'])
      {
        DropArea['children'].forEach(elem=>{
      
          console.log('type of drop area',elem['type'],'drag area',DragNode['type'])
          if(elem['type']==DragNode['type'])
          {
            console.log(elem)
            this.handelDragElem(elem,DragNode)
          }
        })
      }
      }
      else {
        console.log(node,DragNode)
          this.handelDragElem(DropArea,DragNode)
          DragNode['parent'] == DropArea['pub_key']
          if (DragNode['type'] == 'resources') {
            this.updateResource(DropArea['pub_key'], DragNode['_id'], node['type'])
          }
          if (DragNode['type'] == 'units') {
            this.updateUnit(DragNode['pub_key'], DropArea['pub_key'], node['type'])
          }
      }
    }) }
    else{
      this.layoutUtilsService.showActionNotification(_unadraggableMessage, MessageType.Read);
    }
    this.dragNode = null;
    this.dragNodeExpandOverNode = null;
    this.dragNodeExpandOverTime = 0;
    console.log(this.dataSource.data)
  
  }

  handleDragEnd(event) {
    this.dragNode = null;
    this.dragNodeExpandOverNode = null;
    this.dragNodeExpandOverTime = 0;
  }


  updateUnit(pub_key, parent, updateArea) {
    if (updateArea == 'branch')
      this.treeclubservice.updateUnitData({ 'pub_key': pub_key, 'branch_key': parent }).subscribe(res => {
      })
    if (updateArea == 'units')
      this.treeclubservice.updateUnitData({ 'pub_key': pub_key, 'units_key': parent }).subscribe(res => {
      })
  }
  updateResource(parent, _id, updatearea) {
    if (updatearea == 'units')
      this.treeclubservice.updateResourceData({ 'units_key': parent, '_id': _id }).subscribe(
        res => { console.log(res) })
    if (updatearea == 'branch')
      this.treeclubservice.updateResourceData({ 'branch_key': parent, '_id': _id }).subscribe(
        res => { console.log(res) })


  }
  handelDragElem(node, dragElem) {
    let newItem: TodoItemNode;
    if (this.dragNodeExpandOverArea === 'above') {
      newItem = this.database.copyPasteItemAbove(dragElem,node);
    } else if (this.dragNodeExpandOverArea === 'below') {
      newItem = this.database.copyPasteItemBelow(dragElem, node);
    } else {
      newItem = this.database.copyPasteItem(dragElem,node);
    }
    this.database.deleteItem(dragElem);
    //  console.log(newItem)
    this.treeControl.expandDescendants(this.nestedNodeMap.get(newItem));
  }
}

