// Angular
import { Component, OnInit, Inject, ChangeDetectionStrategy, OnDestroy } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
// Lodash
import { Router } from '@angular/router';
import * as _lodash from 'lodash';
// RxJS
import { Observable, of, Subscription} from 'rxjs';
// Lodash
import { each, find, some } from 'lodash';
// NGRX
import { Update } from '@ngrx/entity';
import { Store, select } from '@ngrx/store';
// State
import { AppState } from '../../../../../core/reducers';
import { LayoutUtilsService, MessageType, QueryParamsModel } from '../../../../../core/_base/crud';

import { delay } from 'rxjs/operators';
import { Invitation } from './invitations.model';
import { InvitationComponent } from '../invitations.component';
import { UserService } from '../../../../../core/auth/_services';
@Component({
	selector: 'kt-invitations-edit-dialog',
	templateUrl: './invitations-edit.dialog.component.html',
	changeDetection: ChangeDetectionStrategy.Default,
})
export class InvitationsEditDialogComponent implements OnInit, OnDestroy {
	// Public properties
	invitation : Invitation;
	invitation$: Observable<Invitation>;
	hasFormErrors: boolean = false;
	errormessage : string ='';
	viewLoading: boolean = false;
	loadingAfterSubmit: boolean = false;
	allbrancandunits :[];
	checked=false
	// Private properties
	private componentSubscriptions: Subscription;

	/**
	 * Component constructor
	 *
	 * @param dialogRef: MatDialogRef<InvitationsEditDialogComponent>
	 * @param data: any
	 * @param store: Store<AppState>
	 */
	constructor(public dialogRef: MatDialogRef<InvitationsEditDialogComponent>,
		@Inject(MAT_DIALOG_DATA) public data: any,
		private store: Store<AppState>,private ser :UserService,private router: Router,
		private layoutUtilsService: LayoutUtilsService) { }

	/**
	 * @ Lifecycle sequences => https://angular.io/guide/lifecycle-hooks
	 */
//getallusers
	/**
	 * On init
	 */
	ngOnInit() {
		const newInviation = new Invitation();
		this.invitation$ = of(newInviation);
		this.invitation$.subscribe(res => {
			if (!res) {
				return;
			}
			this.invitation = res;
		});
	if(	this.checkedpermission('getallusers')){
		this.ser.getAllBrachsAndUnits(1,10000).subscribe(
			res=>{
				if(res['result']) this.allbrancandunits = res['data'];
			}
		)}
		else{
		 this.checked =true
		 
		}

	}

	/**
	 * On destroy
	 */
	ngOnDestroy() {
		if (this.componentSubscriptions) {
			this.componentSubscriptions.unsubscribe();
		}
	}



	/**
	 * Save data
	 */
	onSubmit() {
		this.loadingAfterSubmit = true;
		this.viewLoading = true;
        if(this.checkedpermission('sendinvitations')){
		 this.ser.sendInvitations({email:this.invitation.email,user_type:this.invitation.user_type,invitation_key:this.invitation.brancandunits}).subscribe(
		 	res=>{
				console.log(res);
				if(res['result'] > 0){
					this.dialogRef.close({});
					if(this.router.url == '/default/user-management/invitations')
						this.router.navigateByUrl("/default/user-management/invitation");
					else this.router.navigateByUrl("/default/user-management/invitations");
					setTimeout(()=>{
						this.layoutUtilsService.showActionNotification(res['message'], MessageType.Update);
					}, 1000)
				}else{
					this.loadingAfterSubmit = false;
					this.viewLoading = false;
					this.hasFormErrors = true;
					this.errormessage = res['message'];
				}
		 	}
		 )
		}
		else{
          this.checked=true
		}

	}

	checkedpermission(key: String) {
		let checked = false
		if (localStorage.getItem('user')) {

			if (JSON.parse(localStorage.getItem('user'))) {
				let user = JSON.parse(localStorage.getItem('user'))

				if (user['allPrivilidge']) {
					const privilidge = user['allPrivilidge']

					Object.keys(privilidge).forEach(role => {
						Object.keys(privilidge[role]).forEach(pri => {

							let per = (privilidge[role][pri].toString()).trim()
							if (key.trim() == per) {
								checked = true
							}
						})
					})
				}
			}
		}

		return checked
	}



	/**
	 * Close alert
	 *
	 * @param $event: Event
	 */
	onAlertClose($event) {
		this.hasFormErrors = false;
	}


	/** UI */
	/**
	 * Returns component title
	 */
	getTitle(): string {
		return 'New Invitation';
	}

	isTitleValid(): boolean {
		return true;
	}


}
