// Angular
import { Component, OnInit, Inject, ChangeDetectionStrategy, OnDestroy } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
// Lodash
import { Router , ActivatedRoute} from '@angular/router';
import * as _lodash from 'lodash';
// RxJS
import { Observable, of, Subscription} from 'rxjs';
// Lodash
import { each, find, some } from 'lodash';
// NGRX
import { Update } from '@ngrx/entity';
import { Store, select } from '@ngrx/store';
// State
import { AppState } from '../../../../../../../core/reducers';
import { LayoutUtilsService, MessageType, QueryParamsModel } from '../../../../../../../core/_base/crud';

import { delay } from 'rxjs/operators';
import { Shift } from './shifts.model';
import { Shifts,UserService } from '../../../../../../../core/auth/_services';

@Component({
	selector: 'kt-shifts-edit-dialog',
	templateUrl: './shifts-edit.dialog.component.html',
	changeDetection: ChangeDetectionStrategy.Default,
})
export class ShiftEditDialogComponent implements OnInit, OnDestroy {
	// Public properties
	shift : Shift;
	shift$: Observable<Shift>;
	hasFormErrors: boolean = false;
	errormessage : string ='';
	viewLoading: boolean = false;
	loadingAfterSubmit: boolean = false;
	allbrancandunits : [];
	allbranch : any;
	allunits : any;
	// Private properties
	private componentSubscriptions: Subscription;

	/**
	 * Component constructor
	 *
	 * @param dialogRef: MatDialogRef<ShiftEditDialogComponent>
	 * @param data: any
	 * @param store: Store<AppState>
	 */
	constructor(public dialogRef: MatDialogRef<ShiftEditDialogComponent>,
		@Inject(MAT_DIALOG_DATA) public data: any,
		private store: Store<AppState>,private ser :Shifts,private userservice:UserService,private router: Router,private activatedRoute: ActivatedRoute,
		private layoutUtilsService: LayoutUtilsService) { }

	/**
	 * @ Lifecycle sequences => https://angular.io/guide/lifecycle-hooks
	 */

	/**
	 * On init
	 */
	ngOnInit() {
		this.allbranch=[];this.allunits=[];
		const newInviation = new Shift();
		this.shift$ = of(newInviation);
		this.shift$.subscribe(res => {
			if (!res) {
				return;
			}
			this.shift = res;
		});

		this.getbranceandunits();

		if(this.data._id != undefined)
			this.getActivitiesById(this.data);

	}

	branchOnchange(key){
		var allunits = [];
		Object.values(this.allbrancandunits).forEach(function(units) {
			if(units['type'] == 'units' && units['branch_key'] == key) allunits.push(units);
		});
		console.table(key);console.table(allunits);
		this.allunits = allunits;
	}
	/**
	 * On destroy
	 */
	ngOnDestroy() {
		if (this.componentSubscriptions) {
			this.componentSubscriptions.unsubscribe();
		}
	}



	/**
	 * Save data
	 */
	onSubmit() {
		if(this.shift.from_time && this.shift.to_time &&  this.shift.shift_type){
		this.loadingAfterSubmit = true;
		this.viewLoading = true;
		console.table(this.shift);
		if(this.data._id != undefined){
			this.ser.updateshifts(this.data._id,this.shift).subscribe(
 		 	res=>{
 				this.redirctFunction(res['result']);
 		 	}
 		 )
		}else{
			this.shift.user_key = this.data.user_key;
			this.ser.addshifts(this.shift).subscribe(
 		 	res=>{
 				this.redirctFunction(res['result']);
 		 	}
 		 )
		}
	 }else {
		 this.hasFormErrors = true;
		 this.errormessage = "Please Enter all Required Fileds"
	 }

	}


	 redirctFunction(done){
		if(done){
			this.dialogRef.close({});

			if(this.router.url.includes('edits')){
				var url = this.router.url.replace("edits",'edit');
				if(!this.router.url.includes('?shift=true'))
					url = url +'?shift=true';
					
				this.router.navigateByUrl(url);
			}else{
				var url = this.router.url.replace("edit",'edits');
				if(!this.router.url.includes('?shift=true'))
					url = url +'?shift=true';
				this.router.navigateByUrl(url);
			}
			setTimeout(()=>{
				this.layoutUtilsService.showActionNotification("Shift Add Successfully", MessageType.Update);
			}, 1000)
		}else{
			this.loadingAfterSubmit = false;
			this.viewLoading = false;
			this.hasFormErrors = true;
			this.errormessage = 'Shift Doesn\'t Added';
		}
	}

	//get branch and units
	getbranceandunits(){
		this.userservice.getAllBrachsAndUnits(1,10000).subscribe(
			res=>{
				if(res['result'] && res['data'].length > 0){
					this.allbrancandunits = res['data'];
					var branches = [];
					Object.keys(res['data']).forEach(function(key) {
						if(res['data'][key].type=='branch') branches.push(res['data'][key]);
					});
					this.allbranch = branches;
					//console.table(branches);
				}
			}
		)
	}

	//get shifts data
	getActivitiesById(data){
			this.shift.branch_key =  data.branch_key;
			this.shift.units_key =  data.units_key;
			this.shift.resource_id =  data.resource_id;
			this.shift.user_key =  data.user_key;
			this.shift.shift_type =  data.shift_type;
			this.shift.day_per_week =  data.day_per_week;
			this.shift.from_time =  data.from_time;
			this.shift.to_time =  data.to_time;
			this.shift.from_day =  data.from_day;
			this.shift.to_day =  data.to_day;
			this.branchOnchange(this.shift.branch_key);
	}
	/**
	 * Close alert
	 *
	 * @param $event: Event
	 */
	onAlertClose($event) {
		this.hasFormErrors = false;
	}

	/** UI */
	/**
	 * Returns component title
	 */
	getTitle(): string {
		return 'New Shift';
	}

	isTitleValid(): boolean {
		return true;
	}


}
