// Angular
import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule, Routes } from '@angular/router';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';

import { HttpClientModule, HTTP_INTERCEPTORS } from '@angular/common/http';
// NGRX
import { StoreModule } from '@ngrx/store';
import { EffectsModule } from '@ngrx/effects';
// Translate
import { TranslateModule } from '@ngx-translate/core';
import { PartialsModule } from '../../partials/partials.module';
// Services
import { HttpUtilsService, TypesUtilsService, InterceptService, LayoutUtilsService } from '../../../core/_base/crud';
// Shared
import { ActionNotificationComponent } from '../../partials/content/crud';
import { CodePreviewModule } from '../../partials/content/general/code-preview/code-preview.module';
import { MaterialPreviewModule } from '../../partials/content/general/material-preview/material-preview.module';
// Components
import { UserManagementComponent } from './user-management.component';
import { UsersListComponent } from './users/users-list/users-list.component';
import { UserEditComponent } from './users/user-edit/user-edit.component';
import { RolesListComponent } from './roles/roles-list/roles-list.component';
import { InvitationComponent } from './invitations/invitations.component';
import { ShiftsComponent } from './users/_subs/shifts/shifts.component';
import { RoleEditDialogComponent } from './roles/role-edit/role-edit.dialog.component';
import { InvitationsEditDialogComponent } from './invitations/invitations-edit/invitations-edit.dialog.component';
import { ShiftEditDialogComponent } from './users/_subs/shifts/shifts-edit/shifts-edit.dialog.component';
import { ClubTreeComponent } from './club_tree/club_tree.component';
import { UserRolesListComponent } from './users/_subs/user-roles/user-roles-list.component';
import { ChangePasswordComponent } from './users/_subs/change-password/change-password.component';
import { AddressComponent } from './users/_subs/address/address.component';
import { SocialNetworksComponent } from './users/_subs/social-networks/social-networks.component';
import { ClubTreeDataComponent } from './club_tree/_sub/_DataShow/club_tree_data'
//guard
import { PermissionGuard } from '../../../core/auth/_guards/permission.guard'
// Material
import {
	MatInputModule,
	MatPaginatorModule,
	MatProgressSpinnerModule,
	MatSortModule,
	MatTableModule,
	MatSelectModule,
	MatMenuModule,
	MatProgressBarModule,
	MatButtonModule,
	MatCheckboxModule,
	MatDialogModule,
	MatTabsModule,
	MatNativeDateModule,
	MatCardModule,
	MatRadioModule,
	MatIconModule,
	MatDatepickerModule,
	MatExpansionModule,
	MatTreeModule,
	MatAutocompleteModule,
	MAT_DIALOG_DEFAULT_OPTIONS,
	MatSnackBarModule,
	MatTooltipModule
} from '@angular/material';
import {
	usersReducer,
	UserEffects
} from '../../../core/auth';



const routes: Routes = [
	{
		path: '',
		component: UserManagementComponent,
		children: [
			{
				path: '',
				redirectTo: 'roles',
				pathMatch: 'full',
				data: { title: 'roles' },


			},
			{
				path: 'roles',
				component: RolesListComponent,
				data: { title: 'roles' },
				canActivate: [PermissionGuard]
			},
			{
				path: 'invitations',
				component: InvitationComponent,
				data: { title: 'invitations' },
				canActivate: [PermissionGuard]
			}
			,
			{
				path: 'invitation',
				component: InvitationComponent,
				data: { title: 'invitations' },
				canActivate: [PermissionGuard]
			},
			{
				path: 'shifts',
				component: ShiftsComponent,
				data: { title: 'shifts' },
				canActivate: [PermissionGuard]
			},
			{
				path: 'clubtree',
				component: ClubTreeComponent,
				data: { title: 'clubtree' },
				canActivate: [PermissionGuard]
			},
			
			{
				path: 'clubtrees',
				component: ClubTreeComponent,
				data: { title: 'clubtree' },
				canActivate: [PermissionGuard]
			},
			{
				path: 'users',
				component: UsersListComponent
				, data: { title: 'users' },
				canActivate: [PermissionGuard]
			}, {
				path: 'user',
				component: UsersListComponent
				, data: { title: 'users' },
				canActivate: [PermissionGuard]
			},
			{
				path: 'users/:id',
				component: UsersListComponent
				, data: { title: 'users' },
				canActivate: [PermissionGuard]
			},
			{
				path: 'users/add',
				component: UserEditComponent,
				data: { title: 'clubtree' },
				canActivate: [PermissionGuard]
			},
			{
				path: 'users/add:id',
				component: UserEditComponent
				, data: { title: 'addUser' },
				canActivate: [PermissionGuard]
			},
			{

				path: 'users/edit',
				component: UserEditComponent
				, data: { title: 'editUser' },
				canActivate: [PermissionGuard]
			},
			{
				path: 'users/edit/:id',
				component: UserEditComponent
				, data: { title: 'editUser' },
				 canActivate:[PermissionGuard]
			},
			{
				path: 'users/:id/edit/:id',
				component: UserEditComponent
				, data: { title: 'editUser' },
				 canActivate:[PermissionGuard]
			},
			{
				path: 'users/:id/edits/:id',
				component: UserEditComponent
				, data: { title: 'editUser' },
				canActivate:[PermissionGuard]
			},
		]
	}
];

@NgModule({
	imports: [
		CommonModule,
		HttpClientModule,
		PartialsModule,
		RouterModule.forChild(routes),
		StoreModule.forFeature('users', usersReducer),
		EffectsModule.forFeature([UserEffects]),
		FormsModule,
		ReactiveFormsModule,
		TranslateModule.forChild(),
		MatButtonModule,
		MatMenuModule,
		MatSelectModule,
		MatInputModule,
		MatTableModule,
		MatAutocompleteModule,
		MatRadioModule,
		MatIconModule,
		MatNativeDateModule,
		MatProgressBarModule,
		MatDatepickerModule,
		MatCardModule,
		MatPaginatorModule,
		MatSortModule,
		MatCheckboxModule,
		MatProgressSpinnerModule,
		MatSnackBarModule,
		MatExpansionModule,
		MatTabsModule,
		MatTooltipModule,
		MatDialogModule,
		CodePreviewModule,
		MaterialPreviewModule,
		MatTreeModule,
		
	],
	providers: [
		InterceptService,
		{
			provide: HTTP_INTERCEPTORS,
			useClass: InterceptService,
			multi: true
		},
		{
			provide: MAT_DIALOG_DEFAULT_OPTIONS,
			useValue: {
				hasBackdrop: true,
				panelClass: 'kt-mat-dialog-container__wrapper',
				height: 'auto',
				width: '900px'
			}
		},
		HttpUtilsService,
		TypesUtilsService,
		LayoutUtilsService,
		PermissionGuard
	],
	entryComponents: [
		ActionNotificationComponent,
		RoleEditDialogComponent,
		InvitationsEditDialogComponent,
		ShiftEditDialogComponent,
		ClubTreeComponent,
		ClubTreeDataComponent,
		
	],
	declarations: [
		UserManagementComponent,
		UsersListComponent,
		UserEditComponent,
		RolesListComponent,
		InvitationComponent,
		ClubTreeComponent,
		RoleEditDialogComponent,
		UserRolesListComponent,
		ChangePasswordComponent,
		AddressComponent,
		SocialNetworksComponent,
		InvitationsEditDialogComponent,
		ShiftEditDialogComponent,
		ShiftsComponent,
		ClubTreeComponent,
		ClubTreeDataComponent,
		

	]
}
)
export class UserManagementModule { }
