
import { Component, OnInit, Inject, ChangeDetectionStrategy, OnDestroy, ChangeDetectorRef } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';

// Lodash
import * as _lodash from 'lodash';
// RxJS
import { Observable, of, Subscription } from 'rxjs';
// Lodash
import { each, find, some } from 'lodash';
// NGRX
import { Update } from '@ngrx/entity';
import { Store, select } from '@ngrx/store';
// State
import { AppState } from '../../../../../../core/reducers';
// Services and Models
import {
	Role,
	Permission,
	selectRoleById,
	RoleUpdated,
	selectAllPermissions,
	selectAllRoles,
	selectLastCreatedRoleId,
	RoleOnServerCreated,

} from '../../../../../../core/auth';
import { PermissionsService, UserService } from '../../../../../../core/auth/_services'
import { delay } from 'rxjs/operators';
import { Router } from '@angular/router';
@Component({
	selector: 'kt-user-roles-list',
	templateUrl: './user-roles-list.component.html',
	styleUrls: []
})
export class UserRolesListComponent implements OnInit {
	// Public properties
	role: Role;
	role$: Observable<Role>;
	hasFormErrors: boolean = false;
	viewLoading: boolean = false;
	loadingAfterSubmit: boolean = false;
	allPermissions$;
	rolePermissions;
	currentUser;
	selected;
	type;
	user_id;
	// Private properties
	private componentSubscriptions: Subscription;

	/**
	 * Component constructor
	 *
	 * @param dialogRef: MatDialogRef<RoleEditDialogComponent>
	 * @param data: any
	 * @param store: Store<AppState>
	 */
	constructor(
		private store: Store<AppState>,
		private permission: PermissionsService,
		private User: UserService,
		private route: Router,
		private changeDetectorRefs: ChangeDetectorRef) {


	}

	/**
	 * @ Lifecycle sequences => https://angular.io/guide/lifecycle-hooks
	 */

	/**
	 * On init
	 */
	ngOnInit() {

		this.user_id = this.route.url.split('/')[4]
		this.User.getUserbyId(this.user_id).subscribe(res => {
			this.currentUser = res['users'][0];
			this.User.changeuser(res['users'][0])
			this.changeDetectorRefs.detectChanges();
			this.rolePermissions = this.permission.preparePrivilidges()

			this.checkedPermissions()
			this.currentUser.role
			this.permission.getPriviliedgesType().subscribe(res => {

				this.allPermissions$ = res
			
				console.log(this.allPermissions$);
				if (this.currentUser['privilidge']) {
					let type = this.currentUser['privilidge']['type_id'];

					this.allPermissions$['result'].forEach(elem => {
						if (elem['key'] = type) {
							this.selected = elem['type']
						}
					})
				}
			})

		});
	




	}

	/**
	 * On destroy
	 */
	ngOnDestroy() {
		if (this.componentSubscriptions) {
			this.componentSubscriptions.unsubscribe();
		}
	}

	/**
	 * Load permissions
	 */
	loadPermissions() {
		this.allPermissions$.subscribe(_allPermissions => {
			if (!_allPermissions) {
				return;
			}

			const mainPermissions = _allPermissions.filter(el => !el.parentId);
			mainPermissions.forEach((element: Permission) => {
				const hasUserPermission = this.role.permissions.some(t => t === element.id);
				const rootPermission = new Permission();
				rootPermission.clear();
				rootPermission.isSelected = hasUserPermission;
				rootPermission._children = [];
				rootPermission.id = element.id;
				rootPermission.level = element.level;
				rootPermission.parentId = element.parentId;
				rootPermission.title = element.title;
				const children = _allPermissions.filter(el => el.parentId && el.parentId === element.id);
				children.forEach(child => {
					const hasUserChildPermission = this.role.permissions.some(t => t === child.id);
					const childPermission = new Permission();
					childPermission.clear();
					childPermission.isSelected = hasUserChildPermission;
					childPermission._children = [];
					childPermission.id = child.id;
					childPermission.level = child.level;
					childPermission.parentId = child.parentId;
					childPermission.title = child.title;
					rootPermission._children.push(childPermission);
				});
				this.rolePermissions.push(rootPermission);
			});
		});
	}

	/** ACTIONS */
	/**
	 * Returns permissions ids
	 */
	preparePermissionIds(): number[] {
		const result = [];
		each(this.rolePermissions, (_root: Permission) => {
			if (_root.isSelected) {
				result.push(_root.id);
				each(_root._children, (_child: Permission) => {
					if (_child.isSelected) {
						result.push(_child.id);
					}
				});
			}
		});
		return result;
	}

	/**
	 * Returns role for save
	 */
	prepareRole(): Role {
		const _role = new Role();
		_role.id = this.role.id;
		_role.permissions = this.preparePermissionIds();
		// each(this.assignedRoles, (_role: Role) => _user.roles.push(_role.id));
		_role.title = this.role.title;
		_role.isCoreRole = this.role.isCoreRole;
		return _role;
	}

	/**
	 * Save data
	 */
	onSubmit() {
		this.hasFormErrors = false;
		this.loadingAfterSubmit = false;
		/** check form */
		if (!this.isTitleValid()) {
			this.hasFormErrors = true;
			return;
		}

		const editedRole = this.prepareRole();
		if (editedRole.id > 0) {
			this.updateRole(editedRole);
		} else {
			this.createRole(editedRole);
		}
	}

	/**
	 * Update role
	 *
	 * @param _role: Role
	 */
	updateRole(_role: Role) {
		this.loadingAfterSubmit = true;
		this.viewLoading = true;
		/* Server loading imitation. Remove this on real code */
		const updateRole: Update<Role> = {
			id: this.role.id,
			changes: _role
		};
		this.store.dispatch(new RoleUpdated({
			partialrole: updateRole,
			role: _role
		}));

	}

	/**
	 * Create role
	 *
	 * @param _role: Role
	 */
	createRole(_role: Role) {
		this.loadingAfterSubmit = true;
		this.viewLoading = true;
		this.store.dispatch(new RoleOnServerCreated({ role: _role }));
		this.componentSubscriptions = this.store.pipe(
			delay(1000), // Remove this line
			select(selectLastCreatedRoleId)
		).subscribe(res => {
			if (!res) {
				return;
			}

			this.viewLoading = false;

		});
	}

	/**
	 * Close alert
	 *
	 * @param $event: Event
	 */
	onAlertClose($event) {
		this.hasFormErrors = false;
	}

	/**
	 * Check is selected changes
	 *
	 * @param $event: Event
	 * @param permission: Permission
	 */

	isSelectedChanged($event, permission) {
		console.log('permission',permission)
		if (permission._children) {
			permission._children.forEach((elem) => {
				elem['isSelected'] = $event['checked']
			});
		}
		else if(permission.key)
		{
			permission['isSelected'] = $event['checked']
		}
		// if (this.checkedSelectedChildern(permission._children)) {
		// 	permission['isSelected'] = true
		//}
		this.assignRole()
	}

	/** UI */
	/**
	 * Returns component title
	 */
	getTitle(): string {

		// tslint:disable-next-line:no-string-throw
		return `Edit role ''`;


		// tslint:disable-next-line:no-string-thro
	}

	/**
	 * Returns is title valid
	 */
	isTitleValid(): boolean {
		return (this.role && this.role.title && this.role.title.length > 0);
	}
	//this method to checked permissions of user by the all pemissions 

	checkedPermissions() {
console.log('checked permission',this.currentUser)
           const permissions=[]
			if (this.currentUser['privilidge']) {
				if (this.currentUser['privilidge']['privilidge']) {
					const privilidge = JSON.parse(this.currentUser['privilidge']['privilidge'])
                    Object.keys(privilidge).forEach(role=>{
						Object.keys(privilidge[role]).forEach(pri=>{
							permissions.push(privilidge[role][pri])
						})
					})
					
					permissions.forEach(item => {
						this.childernPermission(item)
					})
				}
			}
		
	}


	//this method to combine all permissions
	childernPermission(item) {
		console.log('vgrgr',this.rolePermissions)
		const allpermission = []

		this.rolePermissions.forEach(elem => {
			if (elem['_children']) {
				elem['_children'].forEach(permission => {
					if (item == permission['value']) {
						permission['isSelected'] = true

					}
				})
			}
		})
		return allpermission;
	}
	checkedSelectedChildern(elem) {
		let checked = 1;
		if (elem['_children']) {
			elem['_children'].forEach(permission => {

				checked &= permission['isSelected']

			})
		}
		console.log(checked)
		return checked;

	}

	selectedPermission() {
		let allpermission = '';

		this.rolePermissions.forEach(elem => {
			elem['_children'].forEach(permission => {
				if (permission['isSelected']) {
					allpermission += permission['value'] + ','
				}
			})
		})

		return allpermission;
	}
	assignRole() {
		let privilidge = this.selectedPermission()
		console.log(privilidge)
		let type = this.type
		console.log(type)
		this.User.updateRolesOfcurrentUser(privilidge, type)
	}


	selectedPriviledgeType(event) {
		console.log(this.selected, event)

		this.allPermissions$['result'].forEach(item => {
			if (this.selected == item.type) {
				//access id of role
				this.type = item['_id']
				console.log(this.type, item['privilidge'])

				if (item['privilidge']) {
					let permissions = item['privilidge'].split(',')
				 permissions.forEach(per => {
						this.childernPermission(per)
					})
				}
			}
		})
		this.assignRole()
	}

}



