import { AfterViewInit, AfterViewChecked } from '@angular/core';
// Angular
import { Component ,OnInit, ElementRef, ViewChild, ChangeDetectionStrategy, OnDestroy, ChangeDetectorRef } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
// Material
import { SelectionModel } from '@angular/cdk/collections';
import { MatPaginator, MatSort, MatSnackBar, MatDialog, MatTableDataSource } from '@angular/material';
// RXJS
import { debounceTime, distinctUntilChanged, tap, skip, take, delay, map } from 'rxjs/operators';
import { fromEvent, merge, Observable, of, Subscription } from 'rxjs';
// LODASH
import { each, find } from 'lodash';

// NGRX
import { Store, select } from '@ngrx/store';
import { AppState } from '../../../../../../core/reducers';

// Services
import { LayoutUtilsService, MessageType, QueryParamsModel } from '../../../../../../core/_base/crud';
import { Shifts ,UserService} from '../../../../../../core/auth/_services'
import { ShiftEditDialogComponent } from './shifts-edit/shifts-edit.dialog.component';
import { SubheaderService } from '../../../../../../core/_base/layout';


// Table with EDIT item in MODAL
// ARTICLE for table with sort/filter/paginator
// https://blog.angular-university.io/angular-material-data-table/
// https://v5.material.angular.io/components/table/overview
// https://v5.material.angular.io/components/sort/overview
// https://v5.material.angular.io/components/table/overview#sorting
// https://www.youtube.com/watch?v=NSt9CI3BXv4
@Component({
	selector: 'kt-shifts',
	templateUrl: './shifts.component.html',
	changeDetection: ChangeDetectionStrategy.OnPush
})
export class ShiftsComponent implements OnInit, OnDestroy {
	// Table fields
	dataSource;
	listData:MatTableDataSource<any>;
	selection = new SelectionModel<any>(true, []);
	displayedColumns = [ 'id', 'title' ,'discriptions' ,'type','status', 'actions'];
	@ViewChild(MatPaginator) paginator: MatPaginator;
//@ViewChild(MatSort) sort: MatSort;
	// Filter fields
	@ViewChild('searchInput') searchInput: ElementRef;
	lastQuery: QueryParamsModel;
	SearchKey: string;
	selectedUser;
	hasMessages: boolean = false;
	checkdata: boolean = false;
	assignedRoles : any;
	// Subscriptions
	private subscriptions: Subscription[] = [];

	/**
	 *
	 * @param activatedRoute: ActivatedRoute
	 * @param store: Store<AppState>
	 * @param router: Router
	 * @param layoutUtilsService: LayoutUtilsService
	 * @param subheaderService: SubheaderService
	 */
	constructor(
		private activatedRoute: ActivatedRoute,
		private store: Store<AppState>,
		private router: Router,
		private layoutUtilsService: LayoutUtilsService,
		public dialog: MatDialog,
		private ser: Shifts,private userservice:UserService,
	    private changeDetectorRefs: ChangeDetectorRef

	) { }
	/**
	 * @ Lifecycle sequences => https://angular.io/guide/lifecycle-hooks
	 */

	/**
	 * On init
	 */
	ngOnInit() {
		this.assignedRoles=[];
		this.getallshifts()
	}

	ngAfterViewInit()
	{
		this.getallshifts()

	}
	/**
	 * On Destroy
	 */
	ngOnDestroy() {
		this.subscriptions.forEach(el => el.unsubscribe());
	}

	addShifts() {
			var user_key = this.activatedRoute.snapshot.paramMap.get("id");
			const dialogRef = this.dialog.open(ShiftEditDialogComponent, { data: {user_key} });
	}

	/**
	 * Close alert
	 *
	 * @param $event: Event
	 */
	onAlertClose($event) {
		this.hasMessages = false;
	}


	getallshifts(pages = 1, prepage = 30, search = null) {
		var user_key = this.activatedRoute.snapshot.paramMap.get("id");
		this.ser.getallshifts(user_key,pages, prepage, search).subscribe(
			res => {
				if (res['result']) {
					console.log('dataaa',res['data'])
					this.assignedRoles = res['data'];
				}
			}
		)
	}

	editshifts(data) {
		const dialogRef = this.dialog.open(ShiftEditDialogComponent, { data: data });
	}

	deactiveshifts(id,status,user_key){
		this.ser.updateshifts(id,{status}).subscribe(
		res=>{
			if(this.router.url == '/default/user-management/users/edit/'+user_key+'?shift=true')
				this.router.navigateByUrl("/default/user-management/users/edits/"+user_key+'?shift=true');
			else this.router.navigateByUrl("/default/user-management/users/edit/"+user_key+'?shift=true');
		}
	 )
	}

	applyFilter() {
		this.getallshifts(null, null, this.searchInput.nativeElement.value)
	}
	onSearchClear() {
		this.searchInput.nativeElement.value = ''
		this.applyFilter()
	}
	nextPage(event) {

		this.getallshifts(event.pageIndex + 1, event.pageSize, null)
	}
	//checked if there is inivitations or not
	checkedinvitations() {
		if (this.dataSource) {
			if (this.dataSource.data) {
				return true
			}
			else return false
		}
		else false
	}

	checkedpermission()
	{
		if(localStorage.getItem('user'))
		{
			if(JSON.parse(localStorage.getItem('user'))['allPrivilidge'])
			{

			if(JSON.parse(localStorage.getItem('user'))['allPrivilidge']['Users Mangement'])
			{

			  if(JSON.parse(localStorage.getItem('user'))['allPrivilidge']['Users Mangement']['Invitation'])
			  return true
			}
		}
		}
		return false
	}

}
